body {
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
  font-size: 10px;
}

a {
  cursor: pointer;
}
div.banner-container {
    background-color: #0065B2;
    box-sizing: border-box;
    height: 2rem;
    font-size: 0.9rem;
    left: 0;
    padding: 0.5rem;
    position: fixed;
    text-align: center;
    top: 0;
    width: 100%;
}

div.banner-container span {
    padding: 0.5rem 1rem;
}

div.banner-container span.banner-left--highlights:hover {
    background-color: #0978B2;
}

div.banner-left {
    left: 0;
    position: absolute;
}

div.banner-left span,
div.banner-left a {
    color: white;
    font-weight: bold;
    text-decoration: none;
}

div.banner-right {
    position: absolute;
    right: 0;
    top: 0.5rem;
}
div.OsAppGroupSmall {
    box-sizing: border-box;
    height: 25%;
    margin: 3.125% 3.125%;
    width: 25%;
}

/* set right margin on the last item of each row */
div.OsAppGroupSmall:nth-child(3n) {
    margin-right: 6.25%;
}

/* set left margin on the first item of each row */
div.OsAppGroupSmall:nth-child(3n - 2) {
    margin-left: 6.25%;
}

/* set top margin on the first row of items */
div.OsAppGroupSmall:nth-child(-n + 3) {
    margin-top: 6.25%;
}

/* set bottom margin on the last row of items */
div.OsAppGroupSmall:nth-child(n+7):nth-child(-n+9) {
    margin-bottom: 6.25%;
}

div.OsAppGroupSmallBackground {
    border-radius: 0.4rem;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
}

div.OsAppGroupSmallContainer {
    align-content: flex-start;
    align-items: center;
    background-size: auto;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

div.OsAppGroupBig {
