div.desktopAppGroupWrapper.expanded div.desktopGroupFolder div.desktopGroupFolder_front {
    top: 2.3rem;
    -webkit-transform: rotateX(-35deg);
            transform: rotateX(-35deg);
}
    
div.desktopGroupFolder_back {
    background-color: #B26B00;
    border-radius: 1rem 0 0 1rem;
    display: inline-block;
    height: calc(100% - 5rem);
    left: 4rem;
    position: absolute;
    top: 1rem;
    width: 4rem;
}

div.desktopGroupFolder_back::before {
    content: '';
    border-left: 2rem solid transparent;
    border-bottom: 2rem solid #B26B00;
    height: 0;
    position: absolute;
    right: 0;
    top: -1rem;
    width: 0;
}

div.desktopGroupFolder_back::after {
    background-color: #B26B00;
    border-radius: 0 1rem 0 0;
    content: '';
    display: inline-block;
    height: 6rem;
    left: 4rem;
    position: absolute;
    top: -1rem;
    width: 4rem;
}

