div.desktopGroupFolder_front {
    background-color: #FFA419;
    border-radius: 1rem 0 0 1rem;
    display: inline-block;
    height: calc(100% - 5rem);
    left: 3.8rem;
    position: absolute;
    top: 2rem;
    transition: top 0.4s ease-out, -webkit-transform 0.4s ease-out;
    transition: transform 0.4s ease-out, top 0.4s ease-out;
    transition: transform 0.4s ease-out, top 0.4s ease-out, -webkit-transform 0.4s ease-out;
    width: 4.2rem;
}

div.desktopGroupFolder_front::before {
    content: '';
    border-left: 2rem solid transparent;
    border-bottom: 2rem solid #FFA419;
    height: 0;
    position: absolute;
    right: 0.2rem;
    top: -1rem;
    width: 0;
}

div.desktopGroupFolder_front::after {
    background-color: #FFA419;
    border-radius: 0 1rem 1rem 0;
    content: '';
    display: inline-block;
    height: 6rem;
    left: 4rem;
    position: absolute;
    top: -1rem;
    width: 4.4rem;
}

